import React, { useEffect } from 'react';

interface VideoProps {
  id: string;
  src: string;
  width: string;
  height?: string;
  poster?: string;
  type?: string;
  onPlay?: (id: any) => void;
}

export const VideoPlayer: React.FC<VideoProps> = props => {
  let style = { width: props.width, marginLeft: '-1px' } as any;
  style.height = props.height ? props.height : '100%';
  useEffect(() => {
    const domElement = document.getElementById(props.id) as HTMLElement;
    domElement?.addEventListener('play', function () {
      props.onPlay && props.onPlay(props.id);
    });
  }, [props, props.id]);
  return (
    <>
      {props.src && (
        <video
          id={props.id}
          style={style}
          poster={props.poster}
          controls={true}
          playsInline={true}
          x5-playsinline={'true'}
          x5-video-player-type="h5-page"
          webkit-playsinline={'true'}
          preload="auto"
        >
          <source src={props.src}></source>
        </video>
      )}
    </>
  );
};
